import axios from 'axios';
import articlesData from './data/articles.json';
import friendsData from './data/friends.json';
import toolsData from './data/tools.json';

// axiosAPI
const api = axios.create({
    baseURL: import.meta.env.VITE_API_URL || 'http://localhost:5000', // 
    timeout: 5000, // 
    headers: {
        'Content-Type': 'application/json',
    },
});

// 
api.interceptors.request.use(
    (config) => {
        // token
        return config;
    },
    (error) => {
        return Promise.reject(error);
    }
);

// 
api.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        // 
        console.error('API:', error);
        return Promise.reject(error);
    }
);

// JSON
api.getLocalData = (path) => {
    return new Promise((resolve, reject) => {
        let data;

        // 
        const articleMatch = path.match(/^\/api\/articles\/(\d+)$/);
        if (articleMatch) {
            const articleId = parseInt(articleMatch[1]);
            const article = articlesData.find(a => a.id === articleId);
            if (article) {
                data = article;
            } else {
                reject(new Error('Article not found'));
                return;
            }
        } else {
            // 
            switch (path) {
                case '/api/articles':
                    data = articlesData;
                    break;
                case '/api/friends':
                    data = friendsData;
                    break;
                case '/api/tools':
                    data = toolsData;
                    break;
                default:
                    data = [];
            }
        }

        // axios
        resolve({
            data: data,
            status: 200,
            statusText: 'OK',
            headers: {},
            config: {}
        });
    });
};

// get
const originalGet = api.get;
api.get = function (url, config) {
    // API
    if (url.startsWith('/api/articles/') || ['/api/articles', '/api/friends', '/api/tools'].includes(url)) {
        return this.getLocalData(url);
    }
    // APIaxios
    return originalGet(url, config);
};

// URL
api.staticBaseUrl = '';

export default api;